    const [services_credential_ssl_certificate, setservices_credential_ssl_certificate] = React.useState([]);
    const [fetchedservices_credential_ssl_certificate, setFetchedservices_credential_ssl_certificate] = React.useState(false);

    React.useEffect(() => {
      services_credential_ssl_certificateloadGrid();
    }, [fetchedservices_credential_ssl_certificate]);

    const services_credential_ssl_certificateloadGrid = React.useCallback(async (services_credential_ssl_certificate_certificate, add) => {
      try {
        var attrs = [];
        if(services_credential_ssl_certificate_certificate) {
          if(add) {
            for(var i = 0; i < services_credential_ssl_certificaterowData.services_credential_ssl_certificate.length; i++) {
              var tempId = ((services_credential_ssl_certificaterowData.services_credential_ssl_certificate[i].certificate)?services_credential_ssl_certificaterowData.services_credential_ssl_certificate[i].certificate:"") + "";
              if((window.services_credential_ssl_certificateediting != true && window.services_credential_ssl_certificateediting != "true") || tempId != window.services_credential_ssl_certificaterowId) {
                attrs.push(services_credential_ssl_certificaterowData.services_credential_ssl_certificate[i]);
              }
            }
            var newId = ((services_credential_ssl_certificate_certificate)?services_credential_ssl_certificate_certificate:"") + "";
            attrs.push({
              id: newId,
              certificate: services_credential_ssl_certificate_certificate,
            });
          } else {
            // Must be a delete
            for(var i = 0; i < services_credential_ssl_certificaterowData.services_credential_ssl_certificate.length; i++) {
              if(services_credential_ssl_certificaterowData.services_credential_ssl_certificate[i].certificate !== services_credential_ssl_certificate_certificate ) {
                attrs.push(services_credential_ssl_certificaterowData.services_credential_ssl_certificate[i]);
              }
            }
          }
        } else {
          attrs = services_credential_ssl_certificaterowData.services_credential_ssl_certificate;
        }
        services_credential_ssl_certificaterowData.services_credential_ssl_certificate = attrs;
        const data = {services_credential_ssl_certificate: attrs};
        setservices_credential_ssl_certificate(data.services_credential_ssl_certificate);

        if(services_credential_ssl_certificate_certificate !== undefined && services_credential_ssl_certificate_certificate !== null ) {
            handleChange();

        }

      } catch (e) {
        console.log(e);
      }
      return true;
    });



